.form-page__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: #000000;
  margin: 27px 0 37px 20px;
}


.form-page__description {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  color: #000000;
  margin: 0;
  margin-bottom: 39px;
  margin-left: 20px;
}


@media (min-width: @tablet-width) {
  .form-page__title {
    font-size: 60px;
    line-height: 60px;
    margin: 0 0 54px 70px;
  }

  .form-page__description {
    font-size: 20px;
    line-height: 30px;
    margin-left: 70px;
    margin-bottom: 66px;
  }
}


@media (min-width: @desktop-width) {
  .form-page__title {
    margin-left: 110px;
    margin-bottom: 56px;
  }

  .form-page__description {
    margin-left: 110px;
  }
}
