.bounty {
  position: relative;
  padding: 0 20px;
  width: 100%;
  min-height: 290px;
  background: url("../../img/bounty-bg-image.jpg") no-repeat 20px;
  box-sizing: border-box;
  margin-bottom: 35px;

  @media (min-resolution: 2dppx) {
    background-image: url("../../img/bounty-bg-image@2x.jpg");
    background-size: 280px auto;
  }

  &::after {
    position: absolute;
    content: "";
    width: calc(100% - 40px);
    height: 100%;
    background-color: rgba(104, 183, 56, 0.85);
    z-index: 1;
  }
}


.bounty__title {
  position: absolute;
  left: 78px;
  top: 65%;
  display: block;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #ffffff;
  width: 161px;
  margin: 0;
  z-index: 2;
}


@media (min-width: @tablet-width) {
  .bounty {
    padding: 0;
    background-size: cover;
    background-position: 0;
    margin-bottom: 76px;

    &::after {
      width: 100%;
    }
  }
}


@media (min-width: @desktop-width) {
  .bounty {
    width: 245px;
    background-position: right;
    margin: 0;
    margin-top: 40px;

    &::after {
      width: 100%;
    }
  }
}
